.card {
  width: 320px;
  height: 190.5px;
  perspective: 1500px;
  position: relative;
  margin: 15px;
}

.cardInner {
  width: 100%;
  height: 100%;
  position: relative;
  transition: transform 0.8s;
  transform-style: preserve-3d;
  box-shadow: 0 8px 30px rgba(0, 0, 0, 0.12);
  border-radius: 16px;
}

.card:hover .cardInner {
  transform: rotateY(180deg);
}

.cardFront,
.cardBack {
  position: absolute;
  width: 100%;
  height: 100%;
  -webkit-backface-visibility: hidden;
  backface-visibility: hidden;
  border-radius: 16px;
  overflow: hidden;
  box-shadow: 0px 7px 12px 4px #64636355;
}

.cardFront {
  background: var(--main);
  display: flex;
  flex-direction: column;
}

.imageWrapper {
  height: 100%;
  width: 100%;
  position: relative;
  overflow: hidden;
  display: flex;
}

.cardImage {
  width: 100%;
  height: auto;
  object-fit: cover;
  transition: transform 0.5s ease;
}

.cardOverlay {
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  background: linear-gradient(
    to top,
    rgba(0, 0, 0, 0.7) 0%,
    rgba(0, 0, 0, 0) 100%
  );
  padding: 30px 20px 20px;
  transition: all 0.3s ease;
}

.overlayTitle {
  color: white;
  font-size: 1.5rem;
  font-weight: 700;
  margin: 0;
  text-shadow: 1px 1px 3px rgba(0, 0, 0, 0.3);
}

.tags {
  position: absolute;
  top: 20px;
  left: 20px;
  z-index: 2;
}

.tag {
  background: var(--red-color);
  color: white;
  padding: 4px 10px;
  border-radius: 50px;
  font-size: 0.7rem;
  font-weight: 600;
  box-shadow: 0 2px 8px var(--red-color);
  text-transform: uppercase;
  letter-spacing: 0.5px;
}

.cardBack {
  background: var(--main);
  transform: rotateY(180deg);
  display: flex;
  flex-direction: column;
  padding: 30px;
  justify-content: space-between;
  position: relative;
  z-index: 0;
}

.cardBack::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-image: url("../../../dist/img/ttten.svg");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  z-index: -1;
}

.cardTitle {
  color: var(--font-color);
  font-size: 1.2rem;
  font-weight: 700;
  margin-bottom: 8px;
  position: relative;
  padding-bottom: 8px;
  text-transform: capitalize;
}

.cardTitle:after {
  content: "";
  position: absolute;
  left: 0;
  bottom: 0;
  height: 2px;
  width: 30px;
  background: linear-gradient(90deg, #8f1a1a, #8f1a1a70);
  border-radius: 2px;
}

.cardDescription {
  color: var(---seconde-font-color);
  font-size: 0.85rem;
  line-height: 1.4;
  margin-bottom: 10px;
  flex-grow: 1;
  display: -webkit-box;
  -webkit-line-clamp: 2;
  -webkit-box-orient: vertical;
  overflow: hidden;
  text-overflow: ellipsis;
}

.cardActions {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-top: 8px;
}

.socialLinks {
  display: flex;
  gap: 8px;
}

.socialLink {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 30px;
  height: 30px;
  border-radius: 50%;
  background: white;
  color: #333;
  box-shadow: 0 2px 6px rgba(0, 0, 0, 0.08);
  font-size: 1rem;
  transition: all 0.3s ease;
}

.socialLink:hover {
  background: var(--red-color);
  color: white;
  transform: translateY(-2px);
}

.detailsBtn {
  display: flex;
  align-items: center;
  gap: 4px;
  background: linear-gradient(90deg, #8f1a1a, #8f1a1abd);
  color: white;
  border: none;
  padding: 6px 12px;
  border-radius: 6px;
  font-size: 0.8rem;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s ease;
  box-shadow: 0 2px 8px #8f1a1a70;
}

.detailsBtn:hover {
  transform: translateY(-2px);
  box-shadow: 0 4px 12px #8f1a1a70;
}

.detailsBtn svg {
  font-size: 1rem;
}
