.blogContainer {
  width: 100%;
  border-radius: 12px;
  padding: 2rem;
}

.blogTitle {
  font-weight: 700;
  margin-bottom: 2rem;
  color: var(--font-color);
  font-size: 2.5rem;
}

.titleHighlight {
  color: var(--red-color);
  position: relative;
}

.titleHighlight::after {
  content: "";
  position: absolute;
  width: 100%;
  height: 3px;
  background-color: var(--red-color);
  bottom: -5px;
  left: 0;
}

.playlistsNav {
  display: flex;
  flex-wrap: wrap;
  gap: 10px;
  justify-content: center;
  padding: 0.5rem;
  margin-bottom: 2rem;
  background-color: var(--seconder);
  border-radius: 10px;
  box-shadow: var(--box-shadow);
}

.playlistBtn {
  padding: 0.6rem 1.2rem;
  border-radius: 30px;
  font-weight: 500;
  transition: all 0.3s ease;
  border: none;
}

.playlistBtn:hover {
  background-color: var(--red-color);
  color: white;
  transform: translateY(-2px);
}

.btn-primary.playlistBtn,
.btn-outline-primary.active {
  background-color: var(--red-color);
  color: white;
}

.videoGrid {
  display: flex;
  flex-direction: column;
  gap: 20px;
  max-height: 75vh;
  overflow-y: auto;
  padding: 1rem;
  border-radius: 10px;
}

.loadingGrid {
  min-height: 400px;
  display: flex;
  justify-content: center;
  align-items: center;
}

/* تأثير الظهور التدريجي */
.fadeIn {
  animation: fadeIn 0.5s ease-in-out forwards;
  background-color: var(--seconder);
  box-shadow: var(--box-shadow) !important;
  opacity: 0;
}

@keyframes fadeIn {
  to {
    opacity: 1;
  }
}

.videoWrapper {
  position: relative;
  padding-top: 56.25%;
  width: 100%;
  overflow: hidden;
  border-top-left-radius: 12px;
  border-top-right-radius: 12px;
}

.videoIframe {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  border: none;
}

/* القالب المتجاوب */
@media (max-width: 992px) {
  .videoGrid {
    max-height: 65vh;
  }
}

@media (max-width: 768px) {
  .blogTitle {
    font-size: 2rem;
  }

  .playlistBtn {
    font-size: 0.9rem;
    padding: 0.5rem 1rem;
  }
}

@media (max-width: 576px) {
  .blogContainer {
    padding: 1.5rem 1rem;
  }

  .blogTitle {
    font-size: 1.7rem;
  }

  .playlistBtn {
    font-size: 0.8rem;
    padding: 0.4rem 0.8rem;
  }
}
