.clickRippleOverlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 99999;
  pointer-events: none;
  overflow: hidden;
}

.ripple {
  position: absolute;
  width: 10px;
  height: 10px;
  border-radius: 50%;
  transform: translate(-50%, -50%);
  pointer-events: none;
  animation: rippleEffect 0.8s ease-out forwards;
  opacity: 0.6;
}

@keyframes rippleEffect {
  0% {
    box-shadow: 0 0 0 0 #8f1a1a;
  }
  20% {
    box-shadow: 0 0 0 20px #8f1a1a67;
  }
  100% {
    box-shadow: 0 0 0 40px rgba(255, 255, 255, 0);
  }
}

/* قواعد وسائط للأجهزة ذات الشاشات الصغيرة */
@media (max-width: 768px) {
  @keyframes rippleEffect {
    0% {
      box-shadow: 0 0 0 0 #8f1a1a;
    }
    20% {
      box-shadow: 0 0 0 10px #8f1a1a62;
    }
    100% {
      box-shadow: 0 0 0 30px rgba(255, 255, 255, 0);
    }
  }
}

/* دعم متصفحات Safari وغيرها */
@-webkit-keyframes rippleEffect {
  0% {
    -webkit-box-shadow: 0 0 0 0 #8f1a1a;
  }
  20% {
    -webkit-box-shadow: 0 0 0 20px #8f1a1a79;
  }
  100% {
    -webkit-box-shadow: 0 0 0 40px rgba(255, 255, 255, 0);
  }
}
